:root {
  font-size: 15px;
  --padding-input: 10px 5px;
  --padding-10: 10px;
  --padding-15: 15px;
  --padding-20: 20px;
  --color-1: #f7f6f6;
  --color-2: #f3f3f3;
  --color-3: #ddd;
  --color-4: #626262;
  --color-5: #ededed;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: rgb(97,97,97);
}
